.coin-container {
    display: flex;
    justify-content: center;
  }
  
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #faf6f6;
    width: 900px;
  }
  
  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
  
    min-width: 300px;
  }
  
  .coin h1 {
    font-size: 16px;
    width: 150px;
  }
  
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }
  
  .coin-symbol {
    text-transform: uppercase;
  }
  
  .coin-data {
    display: flex;
    text-align: right;
    justify-content: space-between;
    width: 100%;
  }
  
  .coin-price {
    width: 110px;
  }
  
  .coin-volume {
    width: 155px;
  }
  
  .coin-marketcap {
    width: 230px;
  }
  
  .coin-percent {
    width: 100px;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
 /* Media query for devices with screen width between 320px and 480px */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  /* Styles for the specified screen size range */

  /* Container for the coin row */
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    margin-left: 70%;
    height: 40px;
    border-bottom: 1px solid #d7d7d7;
    width: 100%;
   
    
   
    

    /* Adjusted width to fill the entire width of the screen */
  }

  /* Individual coin element */
  
}
@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (-webkit-min-device-pixel-ratio: 1) {
    .coin-row {
     
      margin-left: 10%;
      
     
      
     
      
  
      /* Adjusted width to fill the entire width of the screen */
    }
}

/* Portrait */
@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (orientation: portrait) 
  and (-webkit-min-device-pixel-ratio: 1) {
    .coin-row {
      margin-left: 20%;
     
      
     
      
  
      /* Adjusted width to fill the entire width of the screen */
    }
}

/* Landscape */
@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (orientation: landscape) 
  and (-webkit-min-device-pixel-ratio: 1) {
    .coin-row {
      margin-left: 10%;
     
      
     
      
  
      /* Adjusted width to fill the entire width of the screen */
    }
}